import { useEffect } from 'react';
import { useState } from 'react';
import ImagesSlide from './imageslide';
import { images } from '../functions/images';
import {
  MainContainer,
  MainText,
  Grid,
  Image,
  ModalContainer,
  ModalContent,
} from './styles';
import CancelIcon from '@mui/icons-material/Cancel';

const Main = () => {
  const [modalOpen, setModalOpen] = useState(false);
  const [currentImageIndex, setCurrentImageIndex] = useState(0);

  const openModal = (clickedIndex) => {
    setCurrentImageIndex(clickedIndex);
    setModalOpen(true);
  };

  const closeModal = () => {
    setModalOpen(false);
  };

  useEffect(() => {
    if (modalOpen) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = 'auto';
    }

    return () => {
      document.body.style.overflow = 'auto';
    };
  }, [modalOpen]);
  useEffect(() => {
    if (modalOpen) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = 'auto';
    }

    return () => {
      document.body.style.overflow = 'auto';
    };
  }, [modalOpen]);

  const Modal = ({ isopen, onClose, openModal, currentImageIndex }) => {
    if (!isopen) return null;
    return (
      <ModalContainer>
        <ModalContent>
          <ImagesSlide
            openModal={openModal}
            currentImageIndex={currentImageIndex}
          />
          <CancelIcon
            onClick={onClose}
            style={{
              cursor: 'pointer',
              position: 'absolute',
              top: '3%',
              left: '85%',
            }}
          >
            
          </CancelIcon>
        </ModalContent>
      </ModalContainer>
    );
  };

  return (
    <MainContainer>
      <MainText style={{ marginBottom: '5rem' }}>
         |  <br />
            .
        <br />     
        <br />  .
        <br />
      </MainText>
      <MainText></MainText>
      <MainText>   .</MainText>
      <div>
        <Grid>
          {images.map((image, index) => (
            <Image
              key={index}
              src={image}
              alt={`image-${index}`}
              onClick={() => openModal(index)}
            />
          ))}
        </Grid>
        <Modal
          isopen={modalOpen}
          onClose={closeModal}
          openModal={openModal}
          currentImageIndex={currentImageIndex}
        />
      </div>
    </MainContainer>
  );
};

export default Main;
